[class^=icon-]:before, [class*=" icon-"]:before {
  font-family: "custom-icons-font";
  font-style: normal;
  font-weight: normal;
  speak: never;
  display: inline-block;
  text-decoration: inherit;
  width: 1em;
  margin-right: 0.2em;
  text-align: center;
  /* opacity: .8; */
  /* For safety - reset parent styles, that can break glyph codes*/
  font-variant: normal;
  text-transform: none;
  /* fix buttons height, for twitter bootstrap */
  line-height: 1em;
  /* Animation center compensation - margins should be symmetric */
  /* remove if not needed */
  margin-left: 0.2em;
  /* you can be more comfortable with increased icons size */
  /* font-size: 120%; */
  /* Font smoothing. That was taken from TWBS */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* Uncomment for 3D effect */
  /* text-shadow: 1px 1px 1px rgba(127, 127, 127, 0.3); */
}

.icon-figma-settings:before {
  content: "\e800";
}

/* '' */
.icon-figma-plus:before {
  content: "\e801";
}

/* '' */
.icon-figma-tiles:before {
  content: "\e802";
}

/* '' */
.icon-dot:before {
  content: "\e803";
}

/* '' */
.icon-toggle-off:before {
  content: "\f204";
}

/* '' */
.icon-toggle-on:before {
  content: "\f205";
}

/* '' */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

@keyframes popDown {
  from {
    opacity: 0;
    transform: translate(-50%, -200%);
  }
  to {
    opacity: 1;
    transform: translate(-50%, -50%);
  }
}
body {
  font-family: "basier-circle", sans-serif;
  color: #fff;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  margin: 0;
}

i {
  font-family: "custom-icons-font", sans-serif;
}

.btn {
  cursor: pointer;
  color: #fff;
  background: 0;
  border: 0;
}

.widget {
  padding: 8px 10px;
  box-shadow: inset -5px -5px 250px rgba(255, 255, 255, 0.02);
  background: radial-gradient(100% 100% at 0% 0%, rgba(255, 255, 255, 0.4) 0%, rgba(255, 255, 255, 0) 100%);
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-radius: 10px;
}
.widget--hidden {
  display: none;
}

.widget-rounded {
  border-radius: 20px;
}

.card {
  background: #EBEBEB;
  border-radius: 5px;
  padding: 40px;
  color: #000;
}

.link {
  color: #fff;
  text-decoration: none;
}

.icon {
  cursor: pointer;
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.8);
  width: 100%;
  height: 100%;
  z-index: 10;
  display: none;
}
.modal-overlay--visible {
  display: block;
}
.modal-overlay--settings {
  background: #DDE2E4;
}

.modal {
  position: absolute;
  z-index: 9999;
  top: 50%;
  left: 50%;
  animation-name: popDown;
  animation-timing-function: ease;
  animation-duration: 0.3s;
  animation-fill-mode: forwards;
  display: none;
}
.modal--visible {
  display: block !important;
}

.screen-wrapper {
  flex: 1;
  display: grid;
  place-content: center;
}
.screen-wrapper--bg--cloudy {
  background: linear-gradient(180deg, #0F2027 0%, #203A43 52.6%, #2C5364 100%);
}
.screen-wrapper--bg--normal {
  background: #DDE2E4;
}

.screen {
  display: flex;
  flex-direction: column;
  padding: 24px;
  height: 100%;
  width: 100%;
  margin: 0 auto;
  overflow-y: auto;
}
@media only screen and (min-device-width: 768px) {
  .screen--wider {
    width: calc(100% + 17px);
    padding-right: 23px;
  }
}
.screen__header {
  font-weight: normal;
  font-size: 30px;
}
.screen__header--black {
  color: #000;
}
.screen__date {
  color: #EBEBEB;
  font-size: 18px;
  margin-top: 5px;
}
.screen__cities {
  margin: 20px 0 0 0;
}
.screen__city {
  margin: 35px 0;
}
.screen__city-name {
  font-size: 30px;
  position: relative;
  left: -1px;
}
.screen__city-temperature {
  font-size: 40px;
  padding-right: 10px;
}
.screen__city-time {
  font-size: 0.9em;
  color: #EBEBEB;
}
.screen__city-info {
  display: flex;
  justify-content: space-between;
  color: #f2f2f2;
  margin-top: 30px;
}
.screen__city-temperature-range {
  color: #EBEBEB;
  font-size: 20px;
}
.screen__city-weather-condition {
  color: #EBEBEB;
}
.screen__title {
  font-weight: normal;
  display: flex;
  justify-content: space-between;
}
.screen__btn {
  margin: 20px 0 0 0;
  font-size: 3em;
  width: 1.5em;
  min-height: 1.5em;
}
.screen__add-btn {
  align-self: flex-end;
  display: grid;
  place-content: center;
  font-size: 30px;
  width: 75px;
  height: 75px;
}
.screen__weather {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px 0;
}
.screen__temperature {
  margin-top: 15px;
  font-size: 40px;
}
.screen__weather-type {
  font-size: 20px;
  margin-bottom: 40px;
}
.screen__image {
  width: 160px;
}

.city-info-grid {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  grid-gap: 25px 20px;
  margin-bottom: 50px;
}
.city-info-grid__grid-item-wrapper {
  flex: 1 0 44%;
}
.city-info-grid__grid-item {
  flex: 1 0 44%;
  min-height: 100px;
}
.city-info-grid__grid-item:first-child {
  margin-right: 0px;
}
.city-info-grid__grid-item--small-margin {
  margin-bottom: 15px;
}
.city-info-grid__grid-item--no-margin {
  margin: 0;
}
.city-info-grid__grid-item--wide {
  flex: 0 0 100%;
  min-height: 120px;
}
.city-info-grid__widget-description {
  color: #EBEBEB;
  font-size: 15px;
  margin-bottom: 10px;
}
.city-info-grid__content-wrapper {
  font-size: 30px;
}
.city-info-grid__content-wrapper--margin-bottom {
  margin-bottom: 12px;
}
.city-info-grid__widget-additional {
  font-size: 15px;
}

.city-info-grid .city-info-grid__grid-item-wrapper .city-info-grid__grid-item-item {
  margin-right: 0px;
}

.screen-nav-wrapper {
  position: relative;
  height: 823px;
  width: 411px;
  overflow: hidden;
}
@media screen and (max-width: 411px) {
  .screen-nav-wrapper {
    width: 100vw;
  }
}
@media screen and (min-device-width: 512px) {
  .screen-nav-wrapper {
    height: 100vh;
  }
}

.navigation {
  position: absolute;
  z-index: 5;
  bottom: 0;
  right: 0;
  width: 100%;
  border-top: 1px solid #EBEBEB;
  background: rgba(0, 0, 0, 0.08);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 20px;
  font-size: 2em;
}
.navigation__pages {
  display: flex;
}
.navigation__circle {
  font-size: 1.5em;
  color: #EBEBEB;
  width: 20px;
  margin: 0 5px;
  display: grid;
  place-content: center;
}
.navigation__circle--active {
  color: #fff;
}
.navigation__cities-link-wrapper {
  width: 42px;
  height: 42px;
  display: grid;
  place-content: center;
}
.navigation__smaller-icon {
  font-size: 25px;
}

.settings {
  width: 358px;
  background: #EBEBEB;
  box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.25);
  border-radius: 10px;
}
.settings__item {
  display: flex;
  justify-content: space-between;
}
.settings__item-text {
  font-size: 20px;
  line-height: 24px;
}
.settings__toggle-icon {
  cursor: pointer;
  font-size: 2em;
}
.settings__toggle-icon--on {
  color: #689BFF;
}
.settings__toggle-icon--off {
  color: #C4C4C4;
}