@font-face {
  font-family: 'basier-circle';
  src: url('../../assets/fonts/basier-circle/BasierCircle-Regular.woff') format('woff'),
       url('../../assets/fonts/basier-circle/BasierCircle-Bold.woff') format('woff'),
       url('../../assets/fonts/basier-circle/BasierCircle-Bold-Italic.woff') format('woff'),
       url('../../assets/fonts/basier-circle/BasierCircle-Italic.woff') format('woff'),
       url('../../assets/fonts/basier-circle/BasierCircle-Medium.woff') format('woff'),
       url('../../assets/fonts/basier-circle/BasierCircle-MediumItalic.woff') format('woff'),
       url('../../assets/fonts/basier-circle/BasierCircle-SemiBold.woff') format('woff'),
       url('../../assets/fonts/basier-circle/BasierCircle-SemiBoldItalic.woff') format('woff');
}

@font-face {
  font-family: 'custom-icons-font';
  src: url('../../assets/fonts/icon-');
  src: url('../../assets/fonts/icon-#iefix') format('embedded-opentype'),
       url('../../assets/fonts/icon-font/custom-icons-font.woff2?77612995') format('woff2'),
       url('../../assets/fonts/icon-font/custom-icons-font.woff?77612995') format('woff'),
       url('../../assets/fonts/icon-font/custom-icons-font.ttf?77612995') format('truetype'),
       url('../../assets/fonts/icon-font/custom-icons-font.svg?77612995#custom-icons-font') format('svg');
  font-weight: normal;
  font-style: normal;
}
/* Chrome hack: SVG is rendered more smooth in Windozze. 100% magic, uncomment if you need it. */
/* Note, that will break hinting! In other OS-es font will be not as sharp as it could be */
/*
@media screen and (-webkit-min-device-pixel-ratio:0) {
  @font-face {
    font-family: 'custom-icons-font';
    src: url('../font/custom-icons-font.svg?77612995#custom-icons-font') format('svg');
  }
}
*/
[class^="icon-"]:before, [class*=" icon-"]:before {
  font-family: "custom-icons-font";
  font-style: normal;
  font-weight: normal;
  speak: never;

  display: inline-block;
  text-decoration: inherit;
  width: 1em;
  margin-right: .2em;
  text-align: center;
  /* opacity: .8; */

  /* For safety - reset parent styles, that can break glyph codes*/
  font-variant: normal;
  text-transform: none;

  /* fix buttons height, for twitter bootstrap */
  line-height: 1em;

  /* Animation center compensation - margins should be symmetric */
  /* remove if not needed */
  margin-left: .2em;

  /* you can be more comfortable with increased icons size */
  /* font-size: 120%; */

  /* Font smoothing. That was taken from TWBS */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  /* Uncomment for 3D effect */
  /* text-shadow: 1px 1px 1px rgba(127, 127, 127, 0.3); */
}

.icon-figma-settings:before { content: '\e800'; } /* '' */
.icon-figma-plus:before { content: '\e801'; } /* '' */
.icon-figma-tiles:before { content: '\e802'; } /* '' */
.icon-dot:before { content: '\e803'; } /* '' */
.icon-toggle-off:before { content: '\f204'; } /* '' */
.icon-toggle-on:before { content: '\f205'; } /* '' */
